*{margin:0;padding:0;box-sizing:border-box}html,body{height:100%;scroll-behavior:smooth}

.home-window{position:relative;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;width:100%;background-color:#f5f5f5;font-family:"Muli", sans-serif}.home-window .container-header h3{font-size:18px}.home-window .top{position:relative;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;height:50px;width:100%}.home-window .top .new-logo{width:150px;height:150px;margin:10px 0 0 -10px}.home-window .top .new-logo img{width:100%;height:100%}.home-window .top i{position:absolute;right:10px;font-size:15px;cursor:pointer;color:#fff;border:2px solid #F98B48;border-radius:5px;padding:5px;background-color:#F98B48}.home-window .top i:active{-webkit-transform:scale(0.97);transform:scale(0.97);outline:none}.home-window .home-reel{position:relative;display:-webkit-flex;display:flex;height:250px;width:98%;margin-top:10px;border-radius:5px;background:#fff}.home-window .home-reel .slider{overflow:hidden}.home-window .home-reel .slider figure{position:relative;width:500%;height:100%;margin:0;left:0;-webkit-animation:slider 20s infinite;animation:slider 20s infinite}.home-window .home-reel .slider figure img{width:20%;height:100%;float:left}.home-window .deals{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-top:20px;padding:5px;width:96%;border-radius:5px;background-color:#fff}.home-window .deals .container-header{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;width:100%;position:relative}.home-window .deals .container-header h3{font-size:14px}.home-window .deals .container-header i{position:absolute;right:5px;color:rgba(34,32,32,0.6);font-size:13px}.home-window .deals .deals-content{width:100%;height:150px;margin-top:5px;border-radius:5px}.home-window .new-arrivals{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:96%;margin-top:15px;padding:5px;border-radius:5