.main-nav{position:fixed;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;left:0;bottom:0;width:100%;height:50px;box-shadow:2px 2px 10px rgba(0,0,0,0.5);border-radius:5px;z-index:100;background-color:#fff}.main-nav ul{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center;width:100%;height:50px;padding:10px}.main-nav ul .main-navlink{font-size:25px;width:60px;height:50px;text-align:center;padding-top:5px;color:#4E4C67;transition:all 0.3s ease-in-out;text-decoration:none}.main-nav ul .main-navlink:hover{color:#F98B48 !important}.main-nav ul .main-navlink p{font-size:10px;font-family:"Muli", sans-serif}.main-nav .main-navlink-active{border-top:3px solid #F98B48;color:#F98B48 !important}.main-nav .logo{display:none}.main-nav .favourites{display:none}@media screen and (min-width: 650px){.main-nav{position:fixed;top:0;left:0;width:100%;height:8vh;border-radius:0;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.main-nav ul{width:60%;height:80%;display:-webkit-flex;display:flex;margin-left:50px;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center}.main-nav ul .main-navlink{height:100%;width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;padding:10px;margin:0 5px;transition:all 0.2s ease-in-out}.main-nav ul .main-navlink i{display:none}.main-nav ul .main-navlink p{font-size:15px}.main-nav ul .main-navlink:hover{border-bottom:2px solid #985f6f;color:#fff !important;background-color:line}.main-nav ul .main-navlink:hover p{color:#985f6f}.main-nav ul .main-navlink-active{border:none;border-radius:3px;background-image:linear-gradient(75deg, #D5ABB2, #B76E79, #86434D);color:#fff !important}.main-nav ul .main-navlink-active:hover{border:none}.main-nav ul .main-navlink-active:hover p{color:#fff}.main-nav .logo{display:-webkit-flex;display:flex;position:absolute;top:-30px;left:-30px;font-size:22px;font-family:"Monoton", sans-serif;color:#985f6f}.main-nav .favourites{display:-webkit-flex;display:flex;position:absolute;right:20px;font-size:20px;color:#4E4C67;border:2px solid #4E4C67;border-radius:5px;padding:5px;color:#fff;background-color:#4E4C67;cursor:pointer}.main-nav .favourites:active{-webkit-transform:scale(0.97);transform:scale(0.97);outline:none}}

